import React from 'react';
import {
    FormControl, Grid, TextField, Button,
    InputLabel, Select, MenuItem, makeStyles, Divider,
    Typography
} from '@material-ui/core';


const useStyles = makeStyles({
    root: {
        minWidth: 275,
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        marginBottom: 20,
        marginTop: 40
    },
    pos: {
        marginBottom: 12,
    },
    textInput: {
        width: '100%',
        margin: '10px 0'
    },
    formControl: {
        width: '100%'
    }
});


const BeneficiaryForm = props => {
    const classes = useStyles();

    return (
        <form sytle={{ width: '100%' }} onSubmit={e => props.saveBeneficiary(e)}>
            <Typography align="center" variant="h4" >Add Beneficiaries</Typography>
            <div style={{ display: 'flex', justifyContent: 'space-around' }} >
                <div style={{ width: '45%' }}>
                    <FormControl style={{ width: '100%' }} className={classes.formControl} >
                        <TextField className={classes.textInput} required value={props.formValue.name}
                            onChange={e => props.changeInpValue(e, 'name')}
                            id="outlined-basic" label="Full Names" variant="outlined" />
                    </FormControl>
                </div>
                <div style={{ width: '45%' }}>
                    <TextField className={classes.textInput} required value={props.formValue.presentClass}
                        onChange={e => props.changeInpValue(e, 'presentClass')}
                        id="outlined-basic" label="Current Class" variant="outlined" />
                </div>
            </div>
            <div style={{ display: 'flex', justifyContent: 'space-around' }} >
                <div style={{ width: '45%' }} >
                    <Select
                        value={props.formValue.gender}
                        onChange={e => props.changeInpValue(e, 'gender')}
                        required
                        variant="outlined"
                        className={classes.textInput}
                    >
                        <MenuItem value='male'>Male</MenuItem>
                        <MenuItem value='female'>Female</MenuItem>
                        <MenuItem value='others'>Others</MenuItem>
                    </Select>
                </div>
                <div style={{ width: '45%', }}>
                    <TextField className={classes.textInput} required value={props.formValue.age}
                        onChange={e => props.changeInpValue(e, 'age')}
                        id="outlined-basic" type="number" label="Age" variant="outlined" />
                </div>
            </div>
            <Divider style={{ margin: '10px 0' }} />
            <Typography variant="h5">Primary School</Typography>
            <Grid justify="space-around" style={{ margin: '10px 0' }} container spacing={3} >
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.psInstutionname}
                        onChange={e => props.changeInpValue(e, 'psInstutionname')}
                        id="outlined-basic" label="Institution Name" variant="outlined" />
                </div>
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.psCountry}
                        onChange={e => props.changeInpValue(e, 'psCountry')}
                        id="outlined-basic" label="Country" variant="outlined" />
                </div>
            </Grid>
            {/*  */}
            <Divider style={{ margin: '10px 0' }} />
            <Typography variant="h5">Secondary School</Typography>
            <Grid justify="space-around" style={{ margin: '10px 0' }} container spacing={3} >
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.ssInstitutionname}
                        onChange={e => props.changeInpValue(e, 'ssInstitutionname')}
                        id="outlined-basic" label="Institution Name" variant="outlined" />
                </div>
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.ssCountry}
                        onChange={e => props.changeInpValue(e, 'ssCountry')}
                        id="outlined-basic" label="Country" variant="outlined" />
                </div>
            </Grid>
            {/*  */}
            <Divider style={{ margin: '10px 0' }} />
            <Typography variant="h5">Undergraduate</Typography>
            <Grid justify="space-around" style={{ margin: '10px 0' }} container spacing={3} >
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.ugInstitutionname}
                        onChange={e => props.changeInpValue(e, 'ugInstitutionname')}
                        id="outlined-basic" label="Institution Name" variant="outlined" />
                </div>
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.ugCountry}
                        onChange={e => props.changeInpValue(e, 'ugCountry')}
                        id="outlined-basic" label="Country" variant="outlined" />
                </div>
            </Grid>
            {/*  */}
            <Divider style={{ margin: '10px 0' }} />
            <Typography variant="h5">Postgraduate</Typography>
            <Grid justify="space-around" style={{ margin: '10px 0' }} container spacing={3} >
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.pgInstitutionname}
                        onChange={e => props.changeInpValue(e, 'pgInstitutionname')}
                        id="outlined-basic" label="Institution Name" variant="outlined" />
                </div>
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.pgCountry}
                        onChange={e => props.changeInpValue(e, 'pgCountry')}
                        id="outlined-basic" label="Country" variant="outlined" />
                </div>
            </Grid>
            {/*  */}
            <Divider style={{ margin: '10px 0' }} />
            <Typography variant="h5">PhD</Typography>
            <Grid justify="space-around" style={{ margin: '10px 0' }} container spacing={3} >
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.phdInstitutionname}
                        onChange={e => props.changeInpValue(e, 'phdInstitutionname')}
                        id="outlined-basic" label="Institution Name" variant="outlined" />
                </div>
                <div style={{ width: '45%' }} item md={6}>
                    <TextField className={classes.textInput} value={props.formValue.phdCountry}
                        onChange={e => props.changeInpValue(e, 'phdCountry')}
                        id="outlined-basic" label="Country" variant="outlined" />
                </div>
            </Grid>
            {/*  */}
            <Grid container style={{marginBottom: 20}} >
                <Button style={{ marginTop: 20 }}
                    type="submit" variant="outlined" color="primary">ADD BENEFICIARY</Button>
            </Grid>
        </form>
    )
}

export default BeneficiaryForm;