import React, { useState, Fragment } from 'react';
import { Container, Grid, CircularProgress, makeStyles, Button } from '@material-ui/core';
import MuiAlert from '@material-ui/lab/Alert';

import BeneficiaryForm from './BeneficiaryForm';

import { BASE_URL } from "../CONFIG";
import axios from 'axios';
import { withRouter } from 'react-router-dom';


function Alert(props) {
    return <MuiAlert elevation={6} variant="filled" {...props} />;
  }
  
  const useStyles = makeStyles((theme) => ({
    root: {
      width: '100%',
      '& > * + *': {
        marginTop: theme.spacing(2),
      },
    },
  }));


const CreateBeneficiaries = props => {

    const [formValue, updateForm] = useState({
        name: '',
        presentClass: '',
        gender: '',
        age: '',
        phdCountry: '',
        phdInstitutionname: '',
        pgCountry: '',
        pgInstitutionname: '',
        ugCountry: '',
        ugInstitutionname: '',
        ssCountry: '',
        ssInstitutionname: '',
        psCountry: '',
        psInstitutionname: ''
    })

    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);
    const [success, changeSuccess] = useState(false);

    const filterFormValue = () => {
        const obj = {}
        for (let field in formValue) {
            if (formValue[field]) {
                obj[field] = formValue[field];
            }
        }
        return obj;
    }

    const submitBeneficiary = e => {
        e.preventDefault();
        changeError(false);
        changeLoading(true);
        const data = filterFormValue();
        data.sponsorId = localStorage.getItem('sponsorId');
        console.log(data);
        axios.post(BASE_URL + '/bene', data)
            .then(res => {
                changeLoading(false);
                updateForm({
                    name: '',
                    presentClass: '',
                    gender: '',
                    age: '',
                    phdCountry: '',
                    phdInstitutionname: '',
                    pgCountry: '',
                    pgInstitutionname: '',
                    ugCountry: '',
                    ugInstitutionname: '',
                    ssCountry: '',
                    ssInstitutionname: '',
                    psCountry: '',
                    psInstitutionname: ''
                })
                changeSuccess(true);
                setTimeout(() => {
                    changeSuccess(false);
                }, 3000)
            })
            .catch(() => {
                changeLoading(false);
                changeError(true);
                setTimeout(() => {
                    changeError(false);
                }, 3000);
            })
    }

    const finalizeSponsor = e => {
        const data = {
            sponsorId: localStorage.getItem('sponsorId')
        }
        axios.post(BASE_URL+'/mail', data)
        .then(res => {
            localStorage.removeItem('userId');
            props.history.push('/success')
        })
    }

    return (
        <Container>
            {loading ? <Grid container justify="center"><CircularProgress size={55} color="secondary" /></Grid>
                :
                <Fragment>
                    {success?<Alert severity="success">Beneficiary has been added successfully</Alert>:null}
                    {error?<Alert severity="error">Beneficiary was not added!</Alert>:null}
                    <BeneficiaryForm
                        saveBeneficiary={e => submitBeneficiary(e)}
                        changeInpValue={(e, field) => updateForm({ ...formValue, [field]: e.target.value })}
                        formValue={formValue} />
                    <Grid container justify="center" style={{marginBottom: 20}} >
                        <Button onClick={finalizeSponsor} variant="outlined">FINISH REGISTRATION</Button>
                    </Grid>
                </Fragment>
            }
        </Container>
    )
}

export default withRouter(CreateBeneficiaries);