import React, { useState, useEffect, useRef } from 'react';
import {
    Container, Typography, Divider, FormControl, makeStyles,
    TextField, Grid, MenuItem, InputLabel, Select, Button,
    CircularProgress
} from '@material-ui/core';

import { BASE_URL } from '../CONFIG';
import axios from 'axios';
import { withRouter } from 'react-router-dom';


const useStyles = makeStyles({
    root: {
        minWidth: 275,
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        marginBottom: 20,
        marginTop: 40
    },
    pos: {
        marginBottom: 12,
    },
    textInput: {
        width: '100%',
        margin: '10px 0'
    },
    formControl: {
        width: '100%'
    }
});


const CreateSponsor = props => {
    const classes = useStyles();
    const signature = useRef(null);
    
    const [formValue, changeFormValue] = useState({
        name: '',
        nationality: '',
        dob: '1960-01-01',
        gender: '',
        address: '',
        occupation: '',
        maritalStatus: '',
        maidenName: '',
        mobileNumber: '',
        email: '',
        insuranceCover: ''
    })

    const [loading, changeLoading] = useState(false);
    const [error, changeError] = useState(false);

    const changeInpValue = (e, field) => {
        changeFormValue({ ...formValue, [field]: e.target.value })
    }

    const createSponsor = e => {
        e.preventDefault();
        changeLoading(true);
        changeError(false);
        const data = new FormData()
        data.append('name', formValue.name)
        data.append('nationality', formValue.nationality)
        data.append('dob', formValue.dob)
        data.append('gender', formValue.gender)
        data.append('address', formValue.address)
        data.append('occupation', formValue.occupation)
        data.append('maritalStatus', formValue.maritalStatus)
        data.append('maidenName', formValue.maidenName)
        data.append('mobileNumber', formValue.mobileNumber)
        data.append('email', formValue.email)
        data.append('insuranceCover', formValue.insuranceCover)
        data.append('file', signature.current.files[0])
        axios.post(BASE_URL + '/sponsors', data)
            .then(res => {
                changeLoading(false);
                localStorage.setItem('sponsorId', res.data.sponsor._id);
                props.history.push('/create-beneficiaries/');
            })
            .catch(() => {
                changeLoading(false);
                changeError(false);
            })
    }
    let form = (
        <form onSubmit={createSponsor}>
            {error ? <Typography style={{ color: 'tomato' }}>Something went wrong!</Typography> : null}
            <FormControl className={classes.formControl} >
                <Grid container spacing={3} >
                    <Grid item md={6}>
                        <TextField className={classes.textInput} required value={formValue.name}
                            onChange={e => changeInpValue(e, 'name')}
                            id="outlined-basic" label="Full Names" variant="outlined" />
                    </Grid>
                    <Grid item md={6}>
                        <FormControl required className={classes.formControl}>
                            <InputLabel>Nationality</InputLabel>
                            <Select
                                value={formValue.nationality}
                                onChange={e => changeInpValue(e, 'nationality')}
                                required
                                variant="outlined"
                                className={classes.textInput}
                            >
                                <MenuItem value='Nigerian'>Nigerian</MenuItem>
                                <MenuItem value='Others'>Others</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                </Grid>
                <Grid container spacing={3}>
                    <Grid item md={6} >
                        <TextField className={classes.textInput} required value={formValue.dob}
                            onChange={e => changeInpValue(e, 'dob')}
                            id="outlined-basic" label="Date of Birth" type='date' variant="outlined" />
                    </Grid>
                    <Grid item md={6} >
                        <FormControl required className={classes.formControl}>
                            <InputLabel>Gender</InputLabel>
                            <Select
                                value={formValue.gender}
                                onChange={e => changeInpValue(e, 'gender')}
                                required
                                variant="outlined"
                                className={classes.textInput}
                            >
                                <MenuItem value='male'>Male</MenuItem>
                                <MenuItem value='female'>Female</MenuItem>
                                <MenuItem value='others'>Others</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                </Grid>
                <Grid container spacing={3} >
                    <Grid item md={6}>
                        <TextField className={classes.textInput} required value={formValue.address}
                            onChange={e => changeInpValue(e, 'address')}
                            id="outlined-basic" label="Address" variant="outlined" />
                    </Grid>
                    <Grid item md={6}>
                        <TextField className={classes.textInput} required value={formValue.occupation}
                            onChange={e => changeInpValue(e, 'occupation')}
                            id="outlined-basic" label="Occupation/Profession" variant="outlined" />
                    </Grid>
                </Grid>
                <Grid container spacing={3} >
                    <Grid item md={6}>
                        <FormControl required className={classes.formControl}>
                            <InputLabel>Marital Status</InputLabel>
                            <Select
                                value={formValue.maritalStatus}
                                onChange={e => changeInpValue(e, 'maritalStatus')}
                                required
                                variant="outlined"
                                className={classes.textInput}
                            >
                                <MenuItem value='single'>Single</MenuItem>
                                <MenuItem value='married'>Married</MenuItem>
                                <MenuItem value='widowed'>Widowed</MenuItem>
                                <MenuItem value='divorced'>Divorced</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item md={6}>
                        <TextField className={classes.textInput} value={formValue.maidenName}
                            onChange={e => changeInpValue(e, 'maidenName')}
                            id="outlined-basic" label="Maiden Name" variant="outlined" />
                    </Grid>
                </Grid>
                <Grid container spacing={3} >
                    <Grid item md={6}>
                        <TextField className={classes.textInput} required value={formValue.mobileNumber}
                            onChange={e => changeInpValue(e, 'mobileNumber')}
                            id="outlined-basic" label="Mobile Number" variant="outlined" />
                    </Grid>
                    <Grid item md={6}>
                        <TextField className={classes.textInput} required value={formValue.email}
                            onChange={e => changeInpValue(e, 'email')}
                            id="outlined-basic" type="email" label="Email" variant="outlined" />
                    </Grid>
                </Grid>
                <Grid container spacing={3} >
                    <Grid item md={6} >
                        <FormControl required className={classes.formControl}>
                            <InputLabel>Insurrance Cover</InputLabel>
                            <Select
                                value={formValue.insuranceCover}
                                onChange={e => changeInpValue(e, 'insuranceCover')}
                                required
                                variant="outlined"
                                className={classes.textInput}
                            >
                                <MenuItem value='yes'>Yes</MenuItem>
                                <MenuItem value='no'>No</MenuItem>
                            </Select>
                        </FormControl>
                    </Grid>
                    <Grid item md={6} >
                        <p>Upload Signature</p>
                        <input type="file" name="" required ref={signature} id=""/>
                    </Grid>
                </Grid>
            </FormControl>
            <Button style={{ marginTop: 20 }}
                type="submit" variant="outlined" color="primary">SUBMIT</Button>
        </form>
    )
    if (loading) {
        form = <Grid container justify="center"><CircularProgress size={55} color="secondary" /></Grid>
    }
    return (
        <Container>
            <Typography variant="h2">Create Sponsor</Typography>
            <Divider />
            {form}
        </Container>
    )
}

export default withRouter(CreateSponsor);